#CookieProtection{
    position: fixed;
    bottom: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background-color: #333;
    opacity: 0.8;
    color: white;
    padding: 10px;
    z-index: 1000;
}

#Cookies{
    position: fixed;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    width: 100%;
    max-width: var(--frame-max-width);
    background-color: #000000ab;
    color: rgb(255, 255, 255);
    border-radius: 10px;
    border: 1px solid #444;
    box-shadow: rgba(243, 243, 243, 0.2) 0px 0px 6px 2px;
    padding: var(--pad-4x);
    text-align: left;
    z-index: 1000;
    overflow-y: auto;
}

#Cookies h1{
    text-align: center;
    margin-bottom: var(--pad-2x);
}

#Cookies button{
    padding: var(--pad-1x) var(--pad-2x);
    border-radius: 5px;
    border: none;
    background-color: var(--color-main);
    box-shadow: rgba(50, 50, 93, 0.25) 0px 13px 27px -5px;
    color: white;
    cursor: pointer;
    margin: 0 var(--pad-2x);
    margin-top: var(--pad-2x);
    transition: all .2s ease;
} #Cookies button:hover {
    background-color: green;
}

    #DeclineCookies{
        background-color: rgb(255, 36, 36) !important;
    } #DeclineCookies:hover {
        background-color: rgb(99, 15, 15) !important;
    }

#Cookies ul {
    text-align: left;
    display: grid;
    gap: 10px;
    padding: 20px 40px;
}

@media (max-width: 600px) {
    #Cookies{
        left: 0;
        top: 0;
        transform: none;
        height: 100%;
    }
}