.container {
    padding-top: 5rem;
    padding-bottom: 2rem;
    width: 100%;
}
    .header{
        flex-direction: column;
        color: white;
        mix-blend-mode: difference;
    }
    .header h5{
        font-size: 1rem;
        text-transform: uppercase;
        color: var(--gradient-color-4);
        filter: invert(100%);
        font-weight: 800;
        letter-spacing: 1px;
        padding-bottom: 6px;
    }
    .header h2{
        font-size: 2.2rem;
        font-weight: 700;
        text-transform: uppercase;
        letter-spacing: 2px;
        padding-bottom: 6px;
    }
    .header p{
        max-width: 400px;
        text-align: center;
        font-size: 0.8rem;
        font-weight: 400;
        letter-spacing: 1px;
    }
    .header .text-red{
        filter: invert(100%);
    }

@media screen and (max-width: 800px){
    .container{
        padding-top: 6rem;
        padding-bottom: 3rem;
    }
}