#About{
    display: flex;

    justify-content: center;
    align-items: center;
    gap: 20px;
}
    #About div{
        background-color: rgba(255, 255, 255, 0.8);
        box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2);
        padding: 20px;
        border-radius: 20px;
        align-self: stretch;
        width: 50%;
    }
    #About div h2{
        font-size: 1.4em;
        font-weight: 600;
        padding-bottom: 10px;
    }

@media screen and (max-width: 800px){
    #About{
        flex-direction: column;
    }
    #About div{
        width: 100%;
    }
}
