@import url('https://fonts.googleapis.com/css2?family=Fredoka:wght@300..700&family=Poppins:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;1,100&display=swap');


#Banner{
    position: relative;
    height: 600px;
}
    #BannerInner{
        position: absolute;
        color: white;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        text-align: center;
        height: 100%;
        width: 100%;
        font-weight: 300;
        letter-spacing: 2px;
        padding: 0 80px;
        z-index: 1;
    }
    #BannerInner h1{
        font-size: 4em;
        font-weight: 600;
    }

#BannerFlow{
    display: block;
    z-index: 2 !important;
    position: absolute;
    width: 100%;
    height: 100%;
    z-index: 0;
}

#gradient-canvas {
    position: absolute;
    width:100%;
    height:100%;
    --gradient-color-1: #043D5D; 
    --gradient-color-2: #032E46; 
    --gradient-color-3: #23B684;  
    --gradient-color-4: #0F595E;
}
       