#Footer{
    display: flex;
    flex-direction: column;
    gap: 0.5rem;
    background-color: var(--gradient-color-2);
    color: white;
    text-align: center;
    font-size: 0.8rem;
    font-weight: 400;
    font-family: 'Roboto', sans-serif;
    position: relative;
    bottom: 0;
    padding: 20px;
    margin-top: 20px;
    width: 100%;
}
#Footer a {
    color: var(--gradient-color-3);
}