.loader {
    display: block;
    position: relative;
    width:  48px;
    height: 48px;
    background: var(--gradient-color-3);
    transform: rotateX(65deg) rotate(45deg);

    color: var(--gradient-color-2);
    animation: layers1 1s ease infinite alternate;
  }
    .loader:after {
    content: '';
    position: absolute;
    inset: 0;
    background: var(--gradient-color-2);
    opacity: 0.4;
    animation: layerTr 1s ease infinite alternate;
    }

    @keyframes layers1 {
    0% { box-shadow: 0px 0px 0 0px  }
    90% , 100% { box-shadow: 20px 20px 0 -4px  }
    }
    @keyframes layerTr {
    0% { transform:  translate(0, 0) scale(1) }
    100% {  transform: translate(-25px, -25px) scale(1) }
    }



.lds-dual-ring.small {
    display: none;
    height: 12px;
    width: 12px;
}
  .lds-dual-ring.small::after{
      border: 3px solid #fff;
      border-color: #fff transparent #fff transparent;
      margin: -3px; /* need this cause of border */
  }
  .lds-dual-ring.small.blue::after{
      border: 3px solid var(--color-cyan);
      border-color: var(--color-link-on-white) transparent var(--color-link-on-white) transparent;
  }
  .lds-dual-ring {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      pointer-events: none;
      width: 26px;
      height: 26px;
      z-index: 2;
    }
      .lds-dual-ring:after {
          content: " ";
          display: block;
          width: 100%;
          height: 100%;
          margin: 6px;
          border-radius: 50%;
          border: 6px solid #fff;
          border-color: #fff transparent #fff transparent;
          animation: lds-dual-ring 1.2s linear infinite;
      }