#Team{
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
    gap: 20px;
    backdrop-filter: blur(10px);
    padding-top: 40px;
    padding-left: 40px;
    padding-right: 40px;
    box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.2);
    border-radius: 16px;
    text-align: center;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    flex-wrap: wrap;
    max-width: 800px;
}

    .team-member img{
        width: 200px;
        min-height: 200px;
        height: 200px;

        border-radius: 16px;
    }

    .team-member h3 { /*name*/
        width: 100%;
        padding: 6px 0;
        font-weight: 600;
    }
    .team-member p { /*position*/
        width: 100%;
        font-weight: 300;
    }

    .team-member .socials{
        padding: 10px;
        display: flex;
        gap: 10px;
        justify-content: center;
        align-items: center;
    }
    .team-member .socials a{
        display: flex;
        justify-content: center;
        align-items: center;
        width: 36px;
        height: 36px;
        transition: all 0.2s ease;
        border-radius: 6px;
    }
        .team-member .socials a:hover{
            border-radius: 30%;
            background-color: var(--gradient-color-3);
            color: black;
        }
        .team-member .socials a i{
            font-size: 1rem;
        }

@media screen and (max-width: 800px){
    #Team{
        grid-template-columns: 1fr;
        padding: initial 0;
    }
}