:root {
	--background-accent-color: #ffffffcc;
    --accent-color: #00eeff;
    --gradient-color-1: #043D5D; 
    --gradient-color-2: #032E46; 
    --gradient-color-3: #23B684;  
    --gradient-color-4: #0F595E;
    --frame-max-width: 1000px;

    --pad-1x: 10px;
    --pad-2x: 20px;
    --pad-3x: 30px;
    --pad-4x: 40px;

    --color-main: var(--gradient-color-3);
}

#bg-wrap{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 120%;
    z-index: -1;
    background-color: white;
}
#black-bg-filter{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(255, 255, 255, 0.9);
    backdrop-filter: blur(12px);
    -webkit-backdrop-filter: blur(12px);
    z-index: -1;
}

main{
    display: inline-flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    width: 100%;
    height: 100%;
    padding: 20px;
}
    .mainFrame{
        padding: 0 10px;
        max-width: var(--frame-max-width);
    }

#QnAArea{
    display: flex;
    width: 100%;
    flex-direction: column;
    height: fit-content;
}
    #QnAArea h1 {
        width: 100%;
        text-align: center;
        filter: drop-shadow(0px 0px 10px rgba(255, 255, 255));
        padding-bottom: 16px;
    }
    #QnAArea ul{
        display: grid;
        grid-template-columns: 1fr;
        gap: 6px;
        width: 100%;
        height: 100%;
        padding: 20px;
        padding-bottom: 40px;
    }
        #QnAArea ul li{
            padding: 12px 16px;
            color: rgb(255, 255, 255);
            background-color: rgb(255, 255, 255 , 0.8);
            
            backdrop-filter: blur(12px);
            -webkit-backdrop-filter: blur(12px);

            display: flex;
            color: black;
            border-radius: 18px;
            box-shadow: rgba(50, 50, 93, 0.25) 0px 13px 27px -5px, rgba(0, 0, 0, 0.3) 0px 8px 16px -8px;
        }

        #QnAArea ul li:hover{
            background-color: rgb(255, 255, 255 , 1);
            box-shadow: rgba(50, 50, 93, 0.6) 0px 13px 27px -5px, rgba(0, 0, 0, 0.3) 0px 8px 16px -8px;
        }
        #QnAArea ul li.last{
            grid-column: 1 / -1;
        }

        .seperator{
            width: 1px;
            background-color: rgb(0, 0, 0, 0.1);
            margin: -12px 14px;
        }

        .question{
            min-width: 100px;
            font-size: 16px;
            font-weight: bold;
            padding: 2px 0;
            cursor: default;
        }
        .answer{
            display: flex;
            align-items: center;
            font-size: 14px;
            padding: 2px 0;
        }

#GenerateMindMapButtonDiv{
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
}
    #GenerateMindMapButton{
        display: flex;
        justify-content: center;
        align-items: center;
        padding: 12px 20px;
        margin: -12px -12px;
        border-radius: 18px;
        color: black;
        border: 2px solid var(--gradient-color-3);
        box-shadow: rgba(0, 0, 0, 0.3) 0px 25px 50px -12px;
        font-size: 18px;
        font-weight: 400;
        cursor: pointer;
        width: 240px;
        height: 60px;
        background-color: rgb(255, 255, 255 , 0.8);
        transition: all 0.2s ease;
        backdrop-filter: blur(12px);
        -webkit-backdrop-filter: blur(12px);
    }
        #GenerateMindMapButton.loading{
            background-color: transparent;
            border: none;
            width: 100px;
            height: 100px;
        }
        #GenerateMindMapButton:hover{

            background-color: rgb(255, 255, 255 , 1);
            box-shadow: rgba(0, 0, 0, 0.4) 0px 25px 50px -12px;
        }

#MindMapDiv{
    /*display: grid;
    grid-template-columns: 1fr 1fr;
    */
    position: relative;
    border-radius: 16px;
    width: 80vw;
    height: 80vh;
    margin: 40px 80px;
    /*padding: 20px 20px;
    padding-bottom: 36px;*/

    background-color: rgba(255, 255, 255, 0.3);
    backdrop-filter: blur(12px);
    -webkit-backdrop-filter: blur(12px);

    filter: blur(2px);

    transition: all 0.6s ease;

    box-shadow: rgba(50, 50, 93, 0.25) 0px 13px 27px -5px, rgba(0, 0, 0, 0.3) 0px 8px 16px -8px;
    /*
    background-image: radial-gradient(var(--background-accent-color) 1px, transparent 1px);
    background-size: 14px 14px;
    */
}

#MindMapDiv.unlocked{
    filter: none;
}
    #MindMapDiv.unlocked #MindMapBlocker{
        display: none;
    }
    #MindMapBlocker{
        position: absolute;
        width: 100%;
        height: 100%;
        z-index: 10;
    }

    #MindMapDiv h1{
        grid-column: 1 / -1;
        text-align: center;
        padding: 8px;
        padding-top: 0;
    }
    #MindMapDiv .recommendation{
        display: flex;
        flex-direction: column;
        /*height: 40px;*/
        overflow: visible;
        margin: 8px;
        padding: 0 40px;
        border-radius: 6px;
        transition: all 0.6s ease;
        box-shadow: rgba(17, 17, 26, 0.05) 0px 1px 0px, rgba(17, 17, 26, 0.1) 0px 0px 8px;
        background-color: rgba(255, 255, 255, 0.4);

        backdrop-filter: blur(12px);
        -webkit-backdrop-filter: blur(12px);
    }
        #MindMapDiv .recommendation.open{
            height: fit-content;
            padding: 20px 40px;
            box-shadow: rgba(0, 0, 0, 0.12) 0px 1px 3px, rgba(0, 0, 0, 0.24) 0px 1px 2px;
        }
        #MindMapDiv .recommendation p:first-of-type{
            font-size: 22px;
            border-bottom: 1px solid rgba(0, 0, 0, 0.1);
        }

    #MindMapDiv p{
        text-align: center;
        padding: 8px;
    }
    #MindMapDiv ul ul{
        padding-left: 20px;
    }


.node {
    display: flex;
    flex-direction: column;
    text-align: left !important;
    width: fit-content !important;
    width: 800px !important;
    padding: 20px 40px !important;
    margin: 0 !important;
    border-radius: 6px;
    transition: all 0.6s ease;
    box-shadow: rgba(17, 17, 26, 0.05) 0px 1px 0px, rgba(17, 17, 26, 0.1) 0px 0px 8px;
    background-color: rgba(255, 255, 255, 0.8) !important;
    border: none !important;
    overflow: visible;

    backdrop-filter: blur(12px);
    -webkit-backdrop-filter: blur(12px);
}

