#gradient-canvas{
    z-index: -1;
}

.career {
    border: none !important;
}
    .career a{
        z-index: 10;
        position: relative;
        display: flex;
        justify-content: center;
    }

.major{
    min-width: 60px !important;
    max-width: 200px !important;
}

.reportBar{
    position: absolute;
    top: 0;
    right: 0;
    display: flex;
    gap: 8px;
    justify-content: flex-end;
    margin: 20px;
    width: 100%;
    height: 100%;
}
    .reportButton{
        display: flex;
        justify-content: center;
        align-items: center;
        border-radius: 50%;
        width: 28px;
        height: 28px;
        color: white;
        font-size: 12px;
        cursor: pointer;
        transition: all 0.2s ease;
        border: none;
        box-shadow: rgba(0, 0, 0, 0.15) 0px 2px 8px;
        z-index: 0;
        opacity: 0;

        transform: translate(0, 50%);
    }   
        .reportButton.good{
            background-color: #00c700c0;
        }
        .reportButton.bad{
            background-color: rgba(255, 0, 0, 0.8);
        }
        .node:hover .reportButton{
            opacity: 1;
            transform: translate(0, 0);
        }

        .reportButton:hover{
            transform: scale(1.2) !important;
        }

#__react-alert__ div div div {
    /* width: auto !important; */
    -webkit-box-flex: 3;
    -webkit-flex: 3;
    -ms-flex: 3;
    flex: 3;
    text-transform: initial !important;
    font:inherit !important;
    padding: 10px 10px !important;
    }
#__react-alert__ div div div svg:first-of-type {
    margin-left: 5px;
    margin-right: 15px !important;
}
#__react-alert__ div div div button {
    display: none;
}

#mouseEffectCanvas{
    position: fixed;
    top: 0;
    left: 0;
    z-index: -1;
}

@media (max-width: 1100px) {
    :root{
        --frame-max-width: 100vw;
    }
    #MindMapDiv{
        margin: 20px;
    }
}

@media (max-width: 800px) {
    #MindMapDiv {
        display: flex;
        flex-direction: column;
        box-shadow: none;
        height: fit-content !important;
        gap: var(--pad-2x);
    }
    #MindMapDiv .node{
        width: 100% !important;
        margin: 0 0 !important;
    }
}